@-webkit-keyframes cq-slide-down{0%{top:-32px;opacity:0}100%{opacity:1;top:0}}@keyframes cq-slide-down{0%{top:-32px;opacity:0}100%{opacity:1;top:0}}@-webkit-keyframes cq-slide-up{100%{height:0;opacity:0}}@keyframes cq-slide-up{100%{height:0;opacity:0}}.cq-notifications{position:absolute;right:10px;top:20px;z-index:+3;background:transparent;display:flex;flex-direction:column;align-items:flex-end}.cq-notifications .notification{position:relative;margin-top:4px;height:32px;-webkit-animation:cq-slide-down 0.3s;animation:cq-slide-down 0.3s;display:flex;justify-content:flex-start;align-items:center;font-size:14px;border:1px solid #fdd063;border-radius:3px}.cq-notifications .notification.warning{background:#fff8e2}.cq-notifications .notification.success{border-color:#008006;background:#cceea1}.cq-notifications .notification.info{border-color:#009afa;background:#e0f2fe}.cq-notifications .notification.error{border-color:#ff3d38;background:#ffeaee}.cq-notifications .notification.hide{-webkit-animation:cq-slide-up 0.3s forwards;animation:cq-slide-up 0.3s forwards}.cq-notifications .notification .close-icon{background:url("./icons/ic-close.svg") no-repeat scroll center center;cursor:pointer;margin:0px 5px;position:relative;padding:5px}.cq-notifications .notification .ic-icon{position:relative;padding:0 5px}.cq-notifications .notification .icon-notification-warning{background:url("./icons/warning.svg") no-repeat scroll center center}.cq-notifications .notification .icon-notification-error{background:url("./icons/error.svg") no-repeat scroll center center}.cq-notifications .notification .icon-notification-info{background:url("./icons/info.svg") no-repeat scroll center center}.cq-notifications .notification .icon-notification-success{background:url("./icons/success.svg") no-repeat scroll center center}.smartcharts-mobile .cq-notifications{position:relative;right:0px;top:0px}
